    line-height: 150%;
    color: #ffffff;
    margin-top: 1vw;
    text-align: center;
    margin-top: 1.5rem;
  }

  .Section0SubHeaderMobile {
    font-family: "Inter";
    font-style: normal;
    font-weight: 500;
    font-size: 1.25rem;
